import React, { Component } from 'react';
import Identicon from 'identicon.js';
import Logo from '../assets/img/logo.png';
import BNBlogo from '../assets/img/bnb-squre.png';

class Navbar extends Component {
	render() {
		return (
			<nav className='navbar navbar-dark bg-danger navbar-dark p-4 text-monospace'>
				<a
					className='navbar-brand col-sm-3 col-md-2 mr-0'
					href='http://www.dappuniversity.com/bootcamp'
					target='_blank'
					rel='noopener noreferrer'
				>
					<img
						src={Logo}
						alt=''
						height='50'
						width='50'
						className='pe-3'
					/>
					<span className='font-weight-bold'>
						{'   '}
						Deliverer Defi File Sharing
					</span>
				</a>
				<ul className='navbar-nav px-3'>
					<li>
						<small id='account'>
							<a
								target='_blank'
								alt=''
								className='text-white'
								rel='noopener noreferrer'
								href={
									'https://etherscan.io/address/' +
									this.props.account
								}
							>
								{this.props.account.substring(0, 6)}
								{this.props.account.substring(38, 42)}
								<img
									src={BNBlogo}
									height='53'
									width='80'
									alt='bnb-logo'
								/>
							</a>
						</small>
						{this.props.account ? (
							<img
								alt=''
								className='ml-2'
								width='30'
								height='30'
								src={`data:image/png;base64,${new Identicon(
									this.props.account,
									30
								).toString()}`}
							/>
						) : (
							<span></span>
						)}
					</li>
				</ul>
			</nav>
		);
	}
}

export default Navbar;
